#header {
	width: 1200px;
	height: 70px;
	left: calc(100% - 1200px) / 2;
	top: 0;
	line-height: 70px;
	position: fixed;
	z-index: 999;
	background: #fff;
	justify-content: space-between;
	border-bottom: 1px solid #dfdfdf;
	img {
		height: 60px;
	}
	li {
		cursor: pointer;
		font-size: 20px;
		opacity: 0.8;
		&:hover {
			opacity: 1;
		}
	}
	a {
		display: block;
		padding: 0 20px;
		color: #1989fa;
	}
}