div, p, h1, h2, h3, h4, h5, h6, header, section, footer, input, textarea, select,
span, strong, em, i, b, th, td, tr, tbody, thead, table, article, nav, aside, dl,
dt, dd, small, canvas, audio, video, datalist, hgroup, mark, progress, time, a,
address, form, button, ul, li, ol, main, summary, img {
	box-sizing: border-box;
	outline: none;
}
html, body {
	font-family: 'Microsoft Yahei',"sans-serif";
	font-size: 16px;
	-webkit-overflow-scrolling: touch;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	-webkit-text-size-adjust:100%;
	-ms-text-size-adjust:100%;
}
body, figure, p, ul, ol, menu, dd {
	margin: 0;
}
article, aside, details, figcaption, figure, footer,
header, hgroup, main, menu, nav, section, details {
	display: block;
}
audio, canvas, progress, video {
	display: inline-block;
	vertical-align: baseline;
}
audio:not([controls]) {
	display: none;
	height: 0;
}
h1, h2, h3, h4, h5, h6 {
	margin: 0;
	font-weight: bold;
	font-size: 100%;
}
dfn, i {
	font-style: italic;
}
sub, sup {
	position: relative;
	font-size: 75%;
	line-height: 0;
	vertical-align: baseline;
}
sup {
	top: -0.5em;
}
sub {
	bottom: -0.25em;
}
img {
	border: 0;
	border-style: none;
	display: inline-block;
	vertical-align: middle;
}
a {
	color: #333;
	text-decoration: none;
}
a:link, a:hover, a:focus {
	text-decoration: none;
	outline: none;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
ul, ol {
	list-style: none;
}
menu, ul, ol {
	padding: 0;
}
input[type="text"], textarea, select {
	border: 1px solid #999;
	border-radius: 3px;
	height: 34px;
	padding: 0 12px;
	outline: none;
	color: inherit;
}
select, textarea {
	width: 200px;
}
input:disabled , select:disabled, textarea:disabled {
	cursor: not-allowed;
}
textarea {
	resize: none;
	padding: 10px 12px;
}
pre {
	display: block;
	white-space: pre-wrap;
	word-wrap: break-word;
	word-break: break-all;
	background: #f7f7f0;
	padding: 10px 0 0;
	margin: 20px 0;
	line-height: 1.42857143;
	color: #333;
	border: 1px solid #ccc;
	border-radius: 6px;
}
code {
	padding: 0;
	color: inherit;
	white-space: pre-wrap;
	background-color: transparent;
	border-radius: 0;
}